import { navigate, PageProps } from "gatsby";
import React, { FunctionComponent } from "react";
import Head from "../lib/Head";

import MainPage from '../view/squart/component/page/MainPage';

const SQuaRT: FunctionComponent<PageProps> = ({ pageContext }) => {
  return (
    <>
    <Head
      title="SQuaRT"
      description="Club"
    />
    <MainPage
    />
    </>
  );
};

export default SQuaRT;
